import React from 'react';
import { Grid, Typography, Card, Avatar } from '@mui/material';
import { styled } from '@mui/material/styles';

const HomeContainer = styled(Grid)(({ theme }) => ({
  height: '100vh',
  padding: theme.spacing(4),
  backgroundColor: '#f8f8f8',
  textAlign: 'center',
}));

const FeatureCard = styled(Card)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  justifyContent: 'center',
  padding: theme.spacing(2),
  margin: theme.spacing(2),
  borderRadius: theme.spacing(2),
  boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
  background: '#eaeaea',
  color: '#000',
  '&:hover': {
    transform: 'scale(1.05)',
    boxShadow: '0 8px 16px rgba(0, 0, 0, 0.2)',
  },
}));

const StepsCard = styled(Card)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  justifyContent: 'center',
  padding: theme.spacing(4),
  margin: theme.spacing(2),
  borderRadius: theme.spacing(2),
  boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
  background: '#eaeaea',
  color: '#000',
  '&:hover': {
    transform: 'scale(1.05)',
    boxShadow: '0 8px 16px rgba(0, 0, 0, 0.2)',
  },
}));

const StepsContainer = styled(Grid)(({ theme }) => ({
  backgroundColor: '#f8f8f8',
  textAlign: 'center',
  marginTop: theme.spacing(4),
}));

const CircleAvatar = styled(Avatar)(({ theme }) => ({
  width: theme.spacing(6),
  height: theme.spacing(6),
  background: '#1976d2',
  color: '#fff',
  marginBottom: theme.spacing(1),
}));

const Features = () => {
  return (
    <HomeContainer container>
      <Grid item xs={12} sm={6} md={3}>
        <FeatureCard>
          <Typography variant="h4">
            24/7 Service
          </Typography>
          <Typography variant="body1">
            Our service is available 24/7 to satisfy your cravings.
          </Typography>
        </FeatureCard>
      </Grid>
      <Grid item xs={12} sm={6} md={3}>
        <FeatureCard>
          <Typography variant="h4">
            20 Minutes Delivery
          </Typography>
          <Typography variant="body1">
            Enjoy our quick delivery within just 20 minutes.
          </Typography>
        </FeatureCard>
      </Grid>
      <Grid item xs={12} sm={6} md={3}>
        <FeatureCard>
          <Typography variant="h4">
            100% Hygiene
          </Typography>
          <Typography variant="body1">
            We ensure 100% hygiene in preparing and delivering your food.
          </Typography>
        </FeatureCard>
      </Grid>
      <Grid item xs={12} sm={6} md={3}>
        <FeatureCard>
          <Typography variant="h4">
            Easy Ordering
          </Typography>
          <Typography variant="body1">
            Follow these simple steps to order delicious food from us.
          </Typography>
        </FeatureCard>
      </Grid>

      {/* Heading for the Steps Section */}
      <Grid item xs={12}>
        <Typography variant="h3" gutterBottom>
          How to Order
        </Typography>
      </Grid>

      <StepsContainer container>
        <Grid item xs={4}>
          <StepsCard>
            <CircleAvatar>1</CircleAvatar>
            <Typography variant="h5" style={{ marginBottom: '8px' }}>
              Login
            </Typography>
            <Typography variant="body1">Log in to your account or create a new one.</Typography>
          </StepsCard>
        </Grid>
        <Grid item xs={4}>
          <StepsCard>
            <CircleAvatar>2</CircleAvatar>
            <Typography variant="h5" style={{ marginBottom: '8px' }}>
              Select a Meal or Plan
            </Typography>
            <Typography variant="body1">Browse our menu and choose your desired meal or plan.</Typography>
          </StepsCard>
        </Grid>
        <Grid item xs={4}>
          <StepsCard>
            <CircleAvatar>3</CircleAvatar>
            <Typography variant="h5" style={{ marginBottom: '8px' }}>
              Get Your Order
            </Typography>
            <Typography variant="body1">Get your delicious food delivered to your doorstep.</Typography>
          </StepsCard>
        </Grid>
      </StepsContainer>

      {/* You can add more content or sections here */}
    </HomeContainer>
  );
};

export default Features;
