import React, { useState } from 'react';
import { Box, Paper, TextField, Button, Typography, Link, Snackbar } from '@mui/material';
import { styled } from '@mui/material/styles';
import loginimg from '../../Images/loginimg.jpg';
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import apiInstance from './Api';
const PasswordResetPageContainer = styled(Box)(({ theme }) => ({
  height: '100vh',
  background: `url(${loginimg})`,
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  overflow: 'hidden', // Hide any overflowing content (white bars)
}));

const PasswordResetForm = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  width: '25vw',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
}));

const PasswordResetPageLink = styled(Link)(({ theme }) => ({
  marginTop: theme.spacing(1),
  cursor:"pointer"
}));

const PasswordResetPage = () => {
  const navigate = useNavigate();
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState("");
  const [loading, setLoading] = useState(false);
  

  const handleSubmit = async (event) => {
    event.preventDefault();
    setLoading(true)
    if (password === confirmPassword) {
      let url = window.location.href;
      let token = url.split("/");
      token = token[token.length - 1];
      const response = await apiInstance.post(`https://gdwhln-3000.csb.app/user/resetpassword/${token}`, { password, confirmPassword });
      // console.log(response)
      if (response.data.message) {
        setError(response.data.message);
        setTimeout(() => { navigate("/user/forgotpassword") }, 5000)

      }
      else if (response.data.user) {
        navigate("/user/login");
      }
      else {

        setError(response.message);
      }
    }
    else
    {
      setError("Passwords dont Match");
    }
    setLoading(false)
  };

  const handleCloseAlert = () => {
    setError("");
  };

  return (
    <PasswordResetPageContainer id="password-reset-container">
      <PasswordResetForm component="form" validate="true" onSubmit={handleSubmit}>
        <Typography variant="h5" gutterBottom>
          Password Reset
        </Typography>
        <TextField
          label="New Password"
          type="password"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <TextField
          label="Confirm New Password"
          type="password"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
        />
        <Button variant="contained" type="submit" color="primary" fullWidth  disabled={loading}>
          Reset Password
        </Button>
        <PasswordResetPageLink href="/login">Back to Login</PasswordResetPageLink>
      </PasswordResetForm>
      <Snackbar open={error} autoHideDuration={6000} onClose={handleCloseAlert} message={error} />
    </PasswordResetPageContainer>
  );
};

export default PasswordResetPage;
