import React from 'react';
import { styled } from '@mui/material/styles';
import { Grid, Typography, Link, IconButton } from '@mui/material';
import { Facebook, Twitter, Instagram, LinkedIn, GitHub } from '@mui/icons-material';

const FooterContainer = styled(Grid)(({ theme }) => ({
  backgroundColor: '#333',
  color: '#fff',
  padding: theme.spacing(2),
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  height: '4rem', // Set the height to 4 rem
}));

const ContactContainer = styled(Grid)({
  display: 'flex',
  alignItems: 'center',
  '& > *': {
    marginRight: '1rem', // Add margin between email and telephone number
  },
});

const SocialLinksContainer = styled(Grid)({
  display: 'flex',
  alignItems: 'center',
});

const SocialLink = styled(IconButton)(({ theme }) => ({
  marginRight: theme.spacing(1),
}));

const Footer = () => {
  return (
    <FooterContainer container>
      <Grid item>
        <Typography sx={{color:"white"}} variant="h6">Contact Us</Typography>
      </Grid>
      <ContactContainer item>
        <Link href="mailto: support@KloudKitchen.com" color="inherit">
          support@KloudKitchen.com
        </Link>
       
      </ContactContainer>
      <Grid item>
        <Typography sx={{color:"white"}} variant="h6">Follow Us</Typography>
      </Grid>
      <SocialLinksContainer item>
        <SocialLink href="#" color="inherit">
          <Facebook />
        </SocialLink>
        <SocialLink href="#" color="inherit">
          <Twitter />
        </SocialLink>
        <SocialLink href="#" color="inherit">
          <Instagram />
        </SocialLink>
        <SocialLink href="#" color="inherit">
          <LinkedIn />
        </SocialLink>
        <SocialLink href="#" color="inherit">
          <GitHub />
        </SocialLink>
      </SocialLinksContainer>
      <Grid item>
        <Typography sx={{color:"white"}} variant="body2" align="center">
          &copy; {new Date().getFullYear()} KLD. All rights reserved.
        </Typography>
      </Grid>
    </FooterContainer>
  );
};

export default Footer;
