// src/components/NotFound.js
import React from 'react';
import { Typography, Box, Button } from '@mui/material';
import { styled } from '@mui/material/styles';

const NotFoundContainer = styled(Box)(({ theme }) => ({
  marginTop: theme.spacing(8),
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
}));

const NotFoundTitle = styled(Typography)(({ theme }) => ({
  fontSize: '3rem',
  fontWeight: 'bold',
}));

const NotFoundDescription = styled(Typography)(({ theme }) => ({
  fontSize: '1.2rem',
  marginBottom: theme.spacing(4),
}));

const BackToHomeButton = styled(Button)({
  fontSize: '1rem',
});

const NotFound = () => {
  return (
    <NotFoundContainer>
      <NotFoundTitle variant="h1">404 Not Found</NotFoundTitle>
      <NotFoundDescription variant="body1">
        Sorry, the page you are looking for does not exist.
      </NotFoundDescription>
      <BackToHomeButton variant="outlined" color="primary" href="/">
        Back to Home
      </BackToHomeButton>
    </NotFoundContainer>
  );
};

export default NotFound;
