import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { TextField, Button, Snackbar, Typography, Box, Slider, Grid, Avatar } from '@mui/material';
import { styled } from '@mui/material/styles';
import loginimg from '../../Images/loginimg.jpg';
import apiInstance from '../Auth/Api';
const UpdatePlanPageContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  backgroundImage: `url(${loginimg})`, // Apply the background image
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  height: '110vh',
}));

const FormContainer = styled(Box)(({ theme }) => ({
  marginTop: theme.spacing(2),
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  background: 'white', // White background for form container
  padding: theme.spacing(2),
  borderRadius: theme.spacing(1), // Rounded corners
  width: "30vw",
}));

const SliderContainer = styled(Grid)({
  marginTop: 8, // Reduced the gap
  marginBottom: 8, // Reduced the gap
});

const ImageUploadButton = styled(Button)({
  marginTop: 16,
  backgroundColor: '#4caf50',
  color: 'white',
  '&:hover': {
    backgroundColor: '#45a049',
  },
});

const UpdatePlanButton = styled(Button)({
  marginTop: 16,
});

const ImageUploadContainer = styled(Box)({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  marginTop: 8, // Reduced the gap
  marginBottom: 8, // Reduced the gap
});

const ImagePreview = styled(Avatar)(({ theme }) => ({
  width: theme.spacing(10),
  height: theme.spacing(10),
  marginTop: theme.spacing(1),
}));

const UpdatePlanPage = () => {
  const navigate = useNavigate();
  const [planName, setPlanName] = useState('');
  const [error, setError] = useState('');
  const [planDescription, setPlanDescription] = useState('');
  const [planImage, setPlanImage] = useState(null);
  const [planDuration, setPlanDuration] = useState(1);
  const [planPrice, setPlanPrice] = useState(100);
  const [planDiscount, setPlanDiscount] = useState(0);
  const [errorSnackbarOpen, setErrorSnackbarOpen] = useState(false);

  const handleUpdatePlan =async  () => {
    let url = window.location.href;
    let id = url.split("/");
    id = id[id.length - 1];
    apiInstance.defaults.withCredentials = true
    const response = await apiInstance.patch(`https://gdwhln-3000.csb.app/plans/modifyplan/${id}`,{ name:planName, description:planDescription, duration:planDuration,price:planPrice, discount:planDiscount })
    if (response.data.updateddata) {
      setError("Plan has been Updated");
      setErrorSnackbarOpen(true)
      setTimeout(() => {
        navigate("/plans/allplans");
      }, 5000)
    }
    else if (response.data.message == "login") {
      navigate("/user/login");
    }
    else {
      setError(response.data.message)
      setErrorSnackbarOpen(true);
    }

    setTimeout(() => {
      setErrorSnackbarOpen(false);
      setError("")
    }, 5000)
  };

  const handleSnackbarClose = () => {
    setErrorSnackbarOpen(false);
  };

  return (
    <UpdatePlanPageContainer>
      <FormContainer>
        <Typography variant="h4" gutterBottom>
          Update Plan
        </Typography>
        <TextField
          label="Plan Name"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={planName}
          onChange={(e) => setPlanName(e.target.value)}
        />
        <TextField
          label="Plan Description"
          variant="outlined"
          fullWidth
          required
          multiline
          rows={3} // Reduce the height of the plan description field
          margin="normal"
          value={planDescription}
          onChange={(e) => setPlanDescription(e.target.value)}
        />
        {/* Add file input for plan image */}
        <ImageUploadContainer>
          <label htmlFor="image-upload">
            <ImageUploadButton variant="contained" component="span">
              Upload Plan Image
            </ImageUploadButton>
          </label>
          <input type="file" accept="image/*" style={{ display: 'none' }} id="image-upload" onChange={(e) => setPlanImage(e.target.files[0])} />
          {planImage && <ImagePreview src={URL.createObjectURL(planImage)} alt="Plan Preview" />}
        </ImageUploadContainer>
        <SliderContainer container spacing={2}>
          <Grid item xs={6}>
            <Typography variant="body1">Plan Duration (months)</Typography>
          </Grid>
          <Grid item xs={6}>
            <Slider
              value={planDuration}
              onChange={(e, newValue) => setPlanDuration(newValue)}
              min={1}
              max={12}
              step={1}
              valueLabelDisplay="auto"
            />
          </Grid>
        </SliderContainer>
        <SliderContainer container spacing={2}>
          <Grid item xs={6}>
            <Typography variant="body1">Plan Price ($)</Typography>
          </Grid>
          <Grid item xs={6}>
            <Slider
              value={planPrice}
              onChange={(e, newValue) => setPlanPrice(newValue)}
              min={1}
              max={1000}
              step={1}
              valueLabelDisplay="auto"
            />
          </Grid>
        </SliderContainer>
        <SliderContainer container spacing={2}>
          <Grid item xs={6}>
            <Typography variant="body1">Plan Discount (%)</Typography>
          </Grid>
          <Grid item xs={6}>
            <Slider
              value={planDiscount}
              onChange={(e, newValue) => setPlanDiscount(newValue)}
              min={0}
              max={50}
              step={1}
              valueLabelDisplay="auto"
            />
          </Grid>
        </SliderContainer>
        <UpdatePlanButton variant="outlined" onClick={handleUpdatePlan}>
          Update Plan
        </UpdatePlanButton>
      </FormContainer>
      <Snackbar open={errorSnackbarOpen} autoHideDuration={6000} onClose={handleSnackbarClose} message={error} />
    </UpdatePlanPageContainer>
  );
};

export default UpdatePlanPage;
