// src/contexts/AuthContext.js
import React, { createContext, useState } from 'react';
import {  useNavigate } from 'react-router-dom';
import apiInstance from '../Components/Auth/Api';
import { bufferToDataURL } from '../Components/Utils/imageUtils';
const AuthContext = createContext();

const AuthProvider = ({ children }) => {
   
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(false);
    const [autherror, setauthError] = useState("");

    const login = async function (email, password) {
        setLoading(true)
        const response = await apiInstance.post(`https://gdwhln-3000.csb.app/user/login`, { email, password });
        console.log(response)
        if (response.data.user != null) {
            setUser(response.data.user);
           
            setLoading(false)
            return true
        }
        else {
            setauthError(response.data.message)
        }
        setTimeout(() => {
            setLoading(false)
            setauthError("")
        }, 5000)
    };
    const signup = async (name, email, password,profileimg) => {
        setLoading(true)
        const response = await apiInstance.post(`https://gdwhln-3000.csb.app/user/signup`, { name, email, password,profileimg:profileimg });
        console.log(response)
        if (response.data.user != null) {
            setUser(response.data.user);
            setLoading(false)
            return true;
        }
        else {
            setauthError(response.data.message)

        }
        setTimeout(() => {
            setLoading(false)
            setauthError("")
        }, 5000)
        return false;

    }

    const logout = async () => {
        // setLoading(true)
        const response = await apiInstance.get(`https://gdwhln-3000.csb.app/user/logout`);
        console.log(response)
        if (response.data.message == "Loggedout") {
            setUser(null);
            return true;
        }
        else {
            setauthError(response.data.message)
        }
        setTimeout(() => {
            // setLoading(false)
            setauthError("")
        }, 5000)
    };

    return (
        <AuthContext.Provider value={{setUser, user, login, logout, autherror, setauthError, signup, loading }}>
            {children}
        </AuthContext.Provider>
    );
};

export { AuthContext, AuthProvider };
