import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import { useTheme } from '@mui/material/styles';
import { useNavigate } from "react-router-dom";
import React, { useState, useContext, useEffect } from 'react';
import { AuthContext } from '../../contexts/AuthContext'
import { bufferToDataURL } from '../Utils/imageUtils';
import pizza from '../../Images/pizza.jpg'
const pages = ['Home', 'Review', 'Plans', 'Sign Up', "Login"];
const settings = ['Update Profile', 'Account', 'Logout'];

function Navbar() {
    const { user, login, logout, autherror, setauthError } = useContext(AuthContext);
    const navigate = useNavigate();
    const theme = useTheme();
    const [anchorElNav, setAnchorElNav] = React.useState(null);
    const [anchorElUser, setAnchorElUser] = React.useState(null);
    const [profile, setProfile] = React.useState(null);
    
    useEffect(() => {
        if (user != null && (user.role == "admin" || user.role == "owner")) {
            if (settings.includes("Manage Plans") == false) {
                settings.push("Manage Plans")
                settings.push("Create Plan")

            }
        }
        if(user)
        {
            bufferToDataURL(user.profileimg.data, user.profileimg.contentType)
            .then((dataURL) => {
              console.log(dataURL)
              setProfile( dataURL );
            })
            .catch((error) => {
              console.error('Error converting image data to data URL:', error);
            });
        }
       
    }, [user])
   
    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };
    const handleOpenUserMenu = (event) => {
        setAnchorElUser(event.currentTarget);
    };

    const handleCloseNavMenu = (key) => {
        console.log(key)
        if (key == "Login") {
            navigate("/user/login");
        }
        else if (key == "Sign Up") {
            navigate("/user/signup");
        }
        else if (key == "Home") {
            navigate("/");
        }
        else if (key == "Plans") {
            navigate("/plans/allplans");
        }
        else if (key == "Review") {
            navigate("/reviews/createreview");
        }
        setAnchorElNav(null);
    };

    const handleCloseUserMenu = async (key) => {
        if (key == "Update Profile") {
            navigate(`/user/updateprofile/${user._id}`);
        }
        else if (key == "Account") {
            navigate("/user/profile");
        }
        else if (key == "Logout") {
            let rv=await logout();
            if(rv)
            navigate("/");
        }
        else if (key == "Manage Plans") {
            navigate("/admin");
        }
        else if (key == "Create Plan") {
          
            navigate("/plans/createplan");
        }
        setAnchorElUser(null)

    };
    const appBarStyle = {
        height: '3.5rem', // Adjust the height as desired
        backgroundColor: theme.palette.primary.contrastText,
        color: theme.palette.primary.main,
        zIndex: 2,
    };
    const containerstyle = {
        height: '3.5rem',
        display: "flex",
        flexdirection: "flex-end",
        alignItems: "center",
        justifyContent: "flex-end",
        padding: 0
        // backgroundColor:"red"
    }
    return (
        <>

            <AppBar position="sticky" style={appBarStyle}>
                <Container position="sticky" maxWidth="xl" style={containerstyle}>

                    <Toolbar sx={{ width: "100vw", padding: 0 }} >

                        <Typography
                            position="relative"
                            padding="0"
                            margin="0"
                            left="10px"
                            variant="h5"
                            noWrap
                            component="a"
                            href="/"
                            sx={{
                                mr: 4,
                                display: { xs: 'none', md: 'flex' },
                                fontFamily: 'cursive',
                                fontWeight: 300,
                                letterSpacing: '.1rem',
                                color: 'inherit',
                                textDecoration: 'none',
                            }}
                        >
                            Kloud Kitchen
                        </Typography>

                        <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}    >
                            {/* to set bugger */}
                            <IconButton

                                size="large"
                                aria-label="account of current user"
                                aria-controls="menu-appbar"
                                aria-haspopup="true"
                                onClick={handleOpenNavMenu}
                                color="inherit"
                            >
                                <MenuIcon />
                            </IconButton>
                            <Menu
                                id="menu-appbar"

                                anchorEl={anchorElNav}
                                anchorOrigin={{
                                    vertical: 'bottom',
                                    horizontal: 'left',
                                }}
                                keepMounted
                                transformOrigin={{
                                    vertical: 'top',
                                    horizontal: 'left',
                                }}
                                open={Boolean(anchorElNav)}
                                onClose={handleCloseNavMenu}
                                sx={{
                                    display: { xs: 'block', md: 'none' },
                                }}
                            >
                                {pages.map((page) => (
                                    <MenuItem key={page} onClick={() => handleCloseNavMenu(page)}>
                                        <Typography textAlign="center">{page}</Typography>
                                    </MenuItem>
                                ))}
                            </Menu>
                        </Box>

                        <Typography
                            variant="h5"
                            noWrap
                            position="relative"
                            component="a"
                            href=""
                            sx={{
                                mr: 2,
                                display: { xs: 'flex', md: 'none' },
                                flexGrow: 1,
                                fontFamily: 'monospace',
                                fontWeight: 700,
                                letterSpacing: '.3rem',
                                color: 'inherit',
                                textDecoration: 'none',
                            }}
                        >
                            Kloud Kitchen
                        </Typography>
                        <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                            {pages.map((page) => (
                                <Button
                                    key={page}
                                    onClick={() => handleCloseNavMenu(page)}
                                    sx={{
                                        my: 1, color: 'white', display: 'block', mx: 0, py: 0, px: 2, '&:hover': {
                                            backgroundColor: '#fff', // Change to the desired hover background color (white)
                                            color: '#000', // Change to the desired hover text color (black)
                                            // Add other hover styles if needed
                                        }
                                    }}
                                >
                                    {page}
                                </Button>
                            ))}
                        </Box>

                        {user!=null && <Box sx={{ flexGrow: 0 }} >
                            <Tooltip title="Open settings">
                                <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                                    <Avatar alt={user.name} src={profile || pizza} />
                                </IconButton>
                            </Tooltip>
                            <Menu
                                sx={{ mt: '45px' }}
                                id="menu-appbar"
                                anchorEl={anchorElUser}
                                anchorOrigin={{
                                    vertical: 'top',
                                    horizontal: 'right',
                                }}
                                keepMounted
                                transformOrigin={{
                                    vertical: 'top',
                                    horizontal: 'right',
                                }}
                                open={Boolean(anchorElUser)}
                                onClose={handleCloseUserMenu}
                            >
                                {settings.map((setting) => (
                                    <MenuItem key={setting} onClick={() => handleCloseUserMenu(setting)}>
                                        <Typography textAlign="center">{setting}</Typography>
                                    </MenuItem>
                                ))}
                            </Menu>
                        </Box>}
                    </Toolbar>
                </Container>
            </AppBar>

        </>
    );
}
export default Navbar;
