import React, { useState ,useContext } from 'react';
import { AuthContext } from '../../contexts/AuthContext'
import { Box,Paper, TextField, Button, Typography, Link, Snackbar } from '@mui/material';
import { styled } from '@mui/material/styles';
import loginimg from '../../Images/loginimg.jpg';

import { useNavigate } from "react-router-dom";
const LoginPageContainer = styled(Box)(({ theme }) => ({
  height: '100vh',
  background: `url(${loginimg})`,
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  overflow: 'hidden', // Hide any overflowing content (white bars)
}));

const LoginForm = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  width: '25vw',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  gap:"1rem"
}));

const LoginLink = styled(Link)(({ theme }) => ({
  marginTop: theme.spacing(1),
  cursor:"pointer"
}));

const LoginPage = () => {
  const navigate = useNavigate();
  const { user, login, loading ,autherror,setauthError} = useContext(AuthContext);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(false);


  const handleSubmit =async (event) => {
    event.preventDefault();
   
    let rv=await login(email,password)
    if(rv)
    {
      navigate("/");
    }
    
  };

  const handleCloseAlert = () => {
    setauthError("");
  };

  return (
    <LoginPageContainer id="login-container">
      <LoginForm component="form" validate="true" onSubmit={handleSubmit}>
        <Typography variant="h5" gutterBottom>
          Login
        </Typography>
        <TextField
          label="Email"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <TextField
          label="Password"
          type="password"
          required
          variant="outlined"
          fullWidth
          margin="normal"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
       
        <Button variant="contained" type="submit" color="primary" fullWidth disabled={loading}>
          Login
        </Button>
        <LoginLink onClick={()=>navigate("/user/forgotpassword")}>Forgot Password?</LoginLink>
        <LoginLink onClick={()=>navigate("/user/signup")}>Don't have an account? Sign Up</LoginLink>
      </LoginForm>
      <Snackbar open={loading} autoHideDuration={6000} onClose={handleCloseAlert} message={autherror} />
    </LoginPageContainer>
  );
};



export default LoginPage
