import React from 'react';
import Modal from '@mui/material/Modal';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Fade from '@mui/material/Fade';

const OrderPlaced = ({ open, onClose }) => {
  return (
    <Modal open={open} onClose={onClose}>
      <Fade in={open} timeout={300}>
        <div style={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          width: 400,
          backgroundColor: '#FFFFFF',
          borderRadius: 8, // Adjust border radius as needed
          padding: 16, // You can also use 'p' instead of 'padding' for consistency
          margin: '0 auto', // Add margin to center the modal horizontally
          outline: 'none', // Remove the default outline
          boxShadow: '0px 2px 8px rgba(0, 0, 0, 0.15)', // Add a subtle box shadow
        }}>
          <IconButton
            edge="end"
            color="inherit"
            onClick={onClose}
            aria-label="close"
            sx={{
              position: 'absolute',
              right: 8,
              top: 8,
            }}
          >
            <CloseIcon />
          </IconButton>
          <Typography variant="h5" gutterBottom>
            Order Placed Successfully!
          </Typography>
          <Typography gutterBottom>
            Thank you for your purchase. Your order has been placed successfully.
          </Typography>
          <Button onClick={onClose}>Close</Button>
        </div>
      </Fade>
    </Modal>
  );
};

export default OrderPlaced;
