import React, { useEffect, useState } from 'react';
import { CardElement,PaymentElement, useStripe, useElements } from '@stripe/react-stripe-js';
import { useParams } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import loginimg from '../../Images/loginimg.jpg';
import apiInstance from '../Auth/Api';
import OrderPlaced from './OrderPlaced';
import PaymentFailed from './Paymentfailed';
import { useNavigate } from "react-router-dom";
const CheckoutFormContainer = styled('div')(({ theme }) => ({
  width: "40vw",
  margin: '0 auto',
  padding: theme.spacing(4),
  border: '1px solid #e1e1e1',
  borderRadius: theme.spacing(2),
  boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  backgroundColor: theme.palette.background.default,
  gap:4
}));

const PlanDetails = styled('div')(({ theme }) => ({
  marginBottom: theme.spacing(2),
  textAlign: 'center',
}));

const PlanName = styled(Typography)(({ theme }) => ({
  fontSize: '18px',
  fontWeight: 'bold',
  marginBottom: theme.spacing(1),
}));

const PlanPrice = styled(Typography)(({ theme }) => ({
  fontSize: '16px',
  color: '#4caf50',
  fontWeight: 'bold',
}));

const CardElementContainer = styled('div')(({ theme }) => ({
  border: '1px solid #d9d9d9',
  padding: theme.spacing(1),
  borderRadius: theme.spacing(1),
  fontSize: '16px',
  width: '100%',
 
}));

const SubmitButton = styled(Button)(({ theme }) => ({
  backgroundColor: '#1a73e8',
  color: '#fff',
  borderRadius: theme.spacing(1),
  padding: theme.spacing(1.5, 2),
  fontSize: '16px',
  fontWeight: 'bold',
  marginTop: theme.spacing(2),
  '&:disabled': {
    backgroundColor: '#b3b3b3',
    cursor: 'not-allowed',
  },
}));

const PageContainer = styled('div')(({ theme }) => ({
  backgroundImage: `url(${loginimg})`,
  backgroundSize: 'cover',
  backgroundRepeat: 'no-repeat',
  height: '100vh',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const CheckoutForm = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  const stripe = useStripe();
  const elements = useElements();
  const [planDetails, setPlanDetails] = useState({
    planName: '',
    planPrice: 0,
  });
  const [address, setAddress] = useState('');
  const [phoneNumber, setPhoneNumber] = useState('');
  const [showOrderPlacedModal, setShowOrderPlacedModal] = useState(false);
  const [showPaymentFailedModal, setShowPaymentFailedModal] = useState(false);

  useEffect(() => {
    const fetchProductDetails = async () => {
      try {
      
        const response = await apiInstance.get(`https://gdwhln-3000.csb.app/checkout/${id}`);
        console.log(response.data.price);
        const name = response.data.name;
        const price=response.data.price.unit_amount
        setPlanDetails({
          planName: name,
          planPrice: (price / 100).toFixed(2),
        });
      } catch (error) {
        console.error('Error fetching product details:', error);
      }
    };

    fetchProductDetails();
  },[id]);

  const handleSubmit = async (event) => {
    event.preventDefault();

    if (!stripe || !elements) {
      return;
    }

    try {
      // console.log(elements.getElement(CardElement));
      const response = await apiInstance.post(`https://gdwhln-3000.csb.app/checkout/${id}`,{amount:Math.round(planDetails.planPrice),currency:"inr", token:elements.getElement(CardElement).id});
      const { clientSecret } = response.data;

      const { error, paymentIntent } = await stripe.confirmCardPayment(clientSecret, {
        payment_method: {
          card: elements.getElement(CardElement),
          billing_details: {
            address: {
              line1: address,
            },
            phone: phoneNumber,
          },
        },
      });

      if (error) {
        console.log('Payment failed:', error.message);
        setShowPaymentFailedModal(true);
      } else {
        console.log('Payment successful:', paymentIntent);
        setShowOrderPlacedModal(true);
      }
    } catch (error) {
      console.error('Error processing payment:', error);
    }
  };
  const handleCloseModal = () => {
    setShowOrderPlacedModal(false);
    setShowPaymentFailedModal(false);
    setTimeout(()=>(navigate("/"),5000));
   
  };

  return (
    <PageContainer>
      <CheckoutFormContainer>
        <Typography variant="h5" gutterBottom>
          Checkout Page
        </Typography>
        <PlanDetails>
          <PlanName>
            Plan: {planDetails.planName}
          </PlanName>
          <PlanPrice>
            Price: {planDetails.planPrice}
          </PlanPrice>
        </PlanDetails>
        <form onSubmit={handleSubmit} style={{ width: '100%' }}>
          <label>
            Card details
            <CardElementContainer>
              <CardElement />
             
            </CardElementContainer>
          </label>
          <TextField
            label="Address"
            variant="outlined"
            value={address}
            onChange={(e) => setAddress(e.target.value)}
            style={{ marginBottom: 16 }}
            fullWidth
          />
          <TextField
            label="Phone Number"
            variant="outlined"
            value={phoneNumber}
            onChange={(e) => setPhoneNumber(e.target.value)}
            style={{ marginBottom: 16 }}
            fullWidth
          />
          <SubmitButton
            type="submit"
            disabled={!stripe}
            variant="contained"
          >
            Pay Now
          </SubmitButton>
        </form>
      </CheckoutFormContainer>
      <OrderPlaced open={showOrderPlacedModal} onClose={handleCloseModal} />
      <PaymentFailed open={showPaymentFailedModal} onClose={handleCloseModal} />
    </PageContainer>
  );
};

export default CheckoutForm;
