import React, { useState, useContext } from 'react';
import { Box, Paper, TextField, Button, Typography, Link, Snackbar, Avatar } from '@mui/material';
import { styled } from '@mui/material/styles';
import loginimg from '../../Images/loginimg.jpg';
import { AuthContext } from '../../contexts/AuthContext'
import { useNavigate } from "react-router-dom";
import apiInstance from './Api';
const SignUpPageContainer = styled(Box)(({ theme }) => ({
  height: '100vh',
  background: `url(${loginimg})`,
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  overflow: 'hidden', // Hide any overflowing content (white bars)
}));

const SignUpForm = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  width: '25vw',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
}));

const SignUpLink = styled(Link)(({ theme }) => ({
  marginTop: theme.spacing(1),
  cursor: "pointer"
}));

const FileInput = styled('input')({
  display: 'none', // Hide the default file input element
});

const SelectProfilePicButton = styled(Button)(({ theme }) => ({
  margin: theme.spacing(2, 0),
  backgroundColor: theme.palette.action.selected
}));

const AvatarContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
}));

const ProfileAvatar = styled(Avatar)(({ theme }) => ({
  width: theme.spacing(10),
  height: theme.spacing(10),
  marginBottom: theme.spacing(2),
}));

const SignUpPage = () => {
  const navigate = useNavigate();
  const { user, setUser, signup, autherror, setauthError } = useContext(AuthContext);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [profilePic, setProfilePic] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");


  const handleSubmit = async (event) => {
    event.preventDefault();
    setLoading(true);
    if (password === confirmPassword) {

      // setLoading(true)
      const response = await apiInstance.post(
        'https://gdwhln-3000.csb.app/user/signup',
        { name, email, password, profileimg: profilePic },
        {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        }
      );
      
      console.log(response)
      if (response.data.user != null) {
        setUser(response.data.user);

      }
      else {
        setauthError(response.data.message)

      }
      setTimeout(() => {
        setLoading(false)
        setauthError("")
      }, 5000)
      return false;


      // let rv=await signup(name,email,password,profilePic)
      // if(rv)
      // {
      //   navigate("/user/login");
      // }
      // else
      // {
      //   console.log(autherror);
      // }
    }
    else {
      setError("Passwords dont match");
      setLoading(true);
      setTimeout(() => {
        setError("");
      }, 5000)
    }
    setLoading(false);
  };

  const handleProfilePicChange = (e) => {
    const file = e.target.files[0];
    setProfilePic(file);
  };

  const handleCloseAlert = () => {
    setError("");
  };

  return (
    <SignUpPageContainer id="signup-container">
      <SignUpForm component="form" validate="true" onSubmit={handleSubmit} enctype="multipart/form-data">
        <Typography variant="h5" gutterBottom>
          Sign Up
        </Typography>
        <TextField
          label="Name"
          variant="outlined"
          required
          fullWidth
          margin="normal"
          value={name}
          onChange={(e) => setName(e.target.value)}
        />
        <TextField
          label="Email"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <TextField
          required
          label="Password"
          type="password"
          variant="outlined"
          fullWidth
          margin="normal"
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />
        <TextField
          label="Confirm Password"
          type="password"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={confirmPassword}
          onChange={(e) => setConfirmPassword(e.target.value)}
        />
        <FileInput type="file" accept="image/*" name="image" onChange={handleProfilePicChange} id="profile-pic-input" />
        <label htmlFor="profile-pic-input">
          <SelectProfilePicButton variant="contained" color="primary" component="span">
            Select Profile Picture
          </SelectProfilePicButton>
        </label>
        {profilePic && (
          <AvatarContainer>
            <ProfileAvatar alt="Profile Picture" src={URL.createObjectURL(profilePic)} />
          </AvatarContainer>
        )}
        <Button type="submit" variant="contained" color="primary" fullWidth disabled={loading}>
          Sign Up
        </Button>
        <SignUpLink onClick={() => navigate("/user/login")}>Already have an account? Log In</SignUpLink>
      </SignUpForm>
      <Snackbar open={error || autherror} autoHideDuration={6000} onClose={handleCloseAlert} message={autherror || error} />
    </SignUpPageContainer>
  );
};

export default SignUpPage;
