import React, { useEffect,useContext, useState } from 'react';
import { useNavigate } from "react-router-dom";
import { Typography, Box, Container, Card, CardContent, Rating, Avatar, Grid } from '@mui/material';
import { styled } from '@mui/material/styles';
import apiInstance from '../Auth/Api';

const ReviewsContainer = styled(Box)(({ theme }) => ({
  marginTop: theme.spacing(4),
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  flexWrap:"nowrap",
  overflowY: 'scroll', // Add scroll functionality to the reviews container
  maxHeight: '50vh', // Limit the height to prevent overflowing
  width:"100vw"
}));

const ReviewCard = styled(Box)(({ theme }) => ({
  width: '70%',
  marginBottom: theme.spacing(2),
  padding: theme.spacing(2),
  backgroundColor:theme.palette.background.default,
  borderRadius:"1rem"
//   height:"55rem"
}));

const ReviewTitle = styled(Typography)(({ theme }) => ({
  fontWeight: 'bold',
  fontSize: '2rem', // Increase heading font size
  color: '#000000', // Change heading text color to black
  marginBottom: theme.spacing(1),
}));

const ReviewDescription = styled(Typography)(({ theme }) => ({
  color: theme.palette.text.secondary,
  marginBottom: theme.spacing(1),
}));

const ReviewAvatar = styled(Avatar)(({ theme }) => ({
  marginRight: theme.spacing(1),
}));

const ReviewDate = styled(Typography)(({ theme }) => ({
  color: theme.palette.text.secondary,
}));
const PlanRating = styled(Rating)(({ theme }) => ({
    marginBottom: theme.spacing(1),
    color: '#000000', // Change star color to black
  }));

const Reviews = () => {
  const [reviews,setReviews]=useState(null);
  useEffect(()=>{
    async function getreviews(){
      let url = window.location.href;
      let id = url.split("/");
      id = id[id.length - 1];
      const response =  await apiInstance.get(`https://gdwhln-3000.csb.app/reviews/${id}`);
      console.log(response)
      if(response.data.message=="reviews received")
      {
        setReviews(response.data.reviews);
        console.log(response.data.reviews);
      }
      else  if(response.data.message=="reviews_not_found")
      {
        console.log("reviews not found")
      }
    }
    getreviews();
  },[])
 

  return (
    <ReviewsContainer>
      <Typography variant="h2" style={{ color: '#ffffff' }} gutterBottom>
        Reviews
      </Typography>
      {reviews && reviews.map((review) => (
        <ReviewCard key={review._id}>
          <Grid container alignItems="center">
            <Grid item>
              <ReviewAvatar alt={review.user.name} src={review.user.name} />
            </Grid>
            <Grid item>
              <ReviewTitle>{review.keyword}</ReviewTitle>
              <ReviewDescription>{review.review}</ReviewDescription>
              <PlanRating name={`rating-${review._id}`} value={review.rating} precision={0.1} readOnly  />
              <ReviewDate>{new Date(review.createdAt).toDateString()}</ReviewDate>
            </Grid>
          </Grid>
        </ReviewCard>
      ))}
    </ReviewsContainer>
  );
};

export default Reviews;
