import React, { useEffect,useContext, useState } from 'react';
import { useNavigate } from "react-router-dom";
import { AuthContext } from '../../contexts/AuthContext'
import { Link } from 'react-router-dom';
import { Typography, Box, Container, Card, CardContent, CardActions, Button, Rating } from '@mui/material';
import { styled } from '@mui/material/styles';
import loginimg from '../../Images/loginimg.jpg';
import apiInstance from '../Auth/Api';
const PlansPageContainer = styled(Container)(({ theme }) => ({
  marginTop: theme.spacing(4),
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
}));

const HeadingContainer = styled(Box)({
  marginBottom: 24,
});

const PlansContainer = styled(Box)(({ theme }) => ({
    width:"100vw",
  display: 'flex',
  flexDirection: 'row', // Display plan cards in a row
  flexWrap: 'wrap', // Allow cards to wrap to the next row if needed
  justifyContent: 'center', // Center the plan cards horizontally
  alignContent:"space-around",
  gap:"5rem"
}));

const PlanCard = styled(Card)(({ theme }) => ({
  width: 300,
  marginBottom: theme.spacing(2),
  borderRadius:"1rem",
  borderRadius: theme.spacing(2),
  boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)', // Add box shadow to the card
  background: '#eaeaea',
  color: '#000',
  transition: 'box-shadow 0.3s ease', // Add transition effect for box shadow
  '&:hover': {
    boxShadow: '0 8px 16px rgba(0, 0, 0, 0.2)', // Add hover effect for box shadow
    cursor: 'pointer', // Change cursor on hover
  },
}));

const PlanCardContent = styled(CardContent)(({ theme }) => ({
  paddingBottom: theme.spacing(0),
}));

const PlanImage = styled('img')(({ theme }) => ({
  width: '100%',
  height: 150, // Reduced the height of the plan card image
  objectFit: 'cover',
}));

const PlanTitle = styled(Typography)(({ theme }) => ({
  fontWeight: 'bold',
}));

const PlanDescription = styled(Typography)(({ theme }) => ({
  marginTop: theme.spacing(2),
  marginBottom: theme.spacing(2),
  color: theme.palette.text.secondary,
}));

const PlanRating = styled(Rating)(({ theme }) => ({
  marginBottom: theme.spacing(1),
}));
const Actions=styled(CardActions)(({theme})=>({
  display:"flex",
  justifyContent:"center"
}))
const SmallButton = styled(Button)({
  fontSize: '0.75rem', // Reduced the font size of the buttons
  marginRight: 8, // Added space between buttons
  marginBottom: 8, // Added space between buttons
});

const PlansPage = () => {
  const navigate = useNavigate();
  const { user} = useContext(AuthContext);
  const [fetchplans,setFetchPlans]=useState(null);
  useEffect(()=>{
    async function getplans(){
      const response =  await apiInstance.get(`https://gdwhln-3000.csb.app/plans/allplans`);
      console.log(response)
      if(response.data.message=="all plans received")
      {
        setFetchPlans(response.data.plans);
        console.log(response.data.plans);
      }
      else  if(response.data.message=="not_found")
      {
        console.log("plans not found")
      }
    }
    getplans();
  },[])
  const plans = [
    {
      id: 1,
      name: 'Plan 1',
      description: 'This is Plan 1 description. Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
      price: 99.99,
      rating: 4.5,
      image: loginimg,
    },
    {
      id: 2,
      name: 'Plan 2',
      description: 'This is Plan 2 description. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris.',
      price: 149.99,
      rating: 3.8,
      image: loginimg,
    },
    {
      id: 3,
      name: 'Plan 3',
      description: 'This is Plan 3 description. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.',
      price: 199.99,
      rating: 4.2,
      image: loginimg,
    },
    // Add more plan data as needed...
  ];

  return (
    <PlansPageContainer>
      <HeadingContainer>
        <Typography variant="h4" gutterBottom>
          Our Offerings
        </Typography>
      </HeadingContainer>
      <PlansContainer>
        {fetchplans && fetchplans.map((plan) => (
          <PlanCard key={plan._id}>
            <PlanCardContent>
              <PlanImage src={loginimg} alt={plan.name} />
              <PlanTitle variant="h5" component="h2">
                {plan.name}
              </PlanTitle>
              <PlanDescription variant="body2">{plan.description}</PlanDescription>
              <PlanRating name={`rating-${plan._id}`} value={plan.ratingAverage} precision={0.1} readOnly />
              <Typography variant="h6">${plan.price.toFixed(2)}</Typography>
            </PlanCardContent>
            <Actions>
              <Link to={`/checkout/${plan.stripeId}`} style={{ textDecoration: 'none' }}>
                <SmallButton variant="contained" color="primary" >
                  Get Now
                </SmallButton>
              </Link>
              <Link to={`/plans/plan/${plan._id}`} style={{ textDecoration: 'none' }}>
                <SmallButton variant="outlined" color="primary">
                  Reviews
                </SmallButton>
              </Link>
            </Actions>
          </PlanCard>
        ))}
      </PlansContainer>
    </PlansPageContainer>
  );
};

export default PlansPage;
