import React, { useRef } from 'react';
import { Grid, Button, Typography } from '@mui/material';
import { styled } from '@mui/material/styles';
import pizza from '../../Images/pizza.jpg';
import Features from './Features';
import HomepagePlans from './HomepagePlans';

import Footer from './Footer';

import Testimonials from './Testimonials';


const HomePageContainer = styled(Grid)(({ theme }) => ({
    height: '92vh',
    position: 'relative',
    textAlign: 'center',
    overflowX: 'hidden', // Disable horizontal scroll
    overflowY: '', // Disable horizontal scroll
  }));

const BackgroundImage = styled('div')(({ theme }) => ({
    position: 'fixed', // Set the background image to stay fixed
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    backgroundImage: `url(${pizza})`,
    zIndex: -1,
}));

const Wrapper = styled('div')(({ theme }) => ({
    height: '100vh',
    display: 'flex',
    position: "relative",
    left: "37vw",
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    position: 'relative', // Add relative positioning to allow z-index to work
    zIndex: 1, // Set a positive z-index to place the content above the background image
    color: '#ffffff',
}));

const Heading = styled(Typography)({
    fontSize: '3rem',
    marginBottom: (theme) => theme.spacing(2),
});

const PlansButton = styled(Button)({
    marginTop: (theme) => theme.spacing(2),
});

const Home = () => {
    const plansRef = useRef(null);

    const handlePlansButtonClick = () => {
        plansRef.current.scrollIntoView({ behavior: 'smooth' });
    };

    return (<>
   
        <HomePageContainer container>
            <BackgroundImage />
            <Wrapper>
                <Heading variant="h1">Meals for Everyone</Heading>
                <PlansButton variant="outlined" color="primary" onClick={handlePlansButtonClick}>
                    Plans
                </PlansButton>
            </Wrapper>
            <Features />
            <div ref={plansRef}>
                <HomepagePlans />
            </div>
            <Testimonials/>
        <Footer />
        </HomePageContainer></>
    );
};

export default Home;
