import './App.css';
import { ThemeProvider } from '@mui/material/styles';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import theme from './theme'; // Import your custom theme
import LoginPage from './Components/Auth/LoginPage';
import Home from './Components/Homepage/Home';
import '@fontsource/roboto/300.css';
import Navbar from './Components/Homepage/Navbar';
import SignUpPage from './Components/Auth/SignUpPage';
import ForgotPasswordPage from './Components/Auth/ForgotPasswordPage';
import PasswordResetPage from './Components/Auth/PasswordResetPage';
import Dashboard from './Components/Homepage/Dashboard';
import UpdateProfilePage from './Components/Auth/UpdateProfilePage';
import CreatePlanPage from './Components/Admin/CreatePlanPage';
import UpdatePlanPage from './Components/Admin/UpdatePlanPage';
import CreateReviewPage from './Components/Homepage/CreateReviewPage';
import UpdateReviewPage from './Components/Homepage/UpdateReviewPage';
import AdminDashboard from './Components/Admin/AdminDashboard';
import PlansPage from './Components/Admin/PlansPage';
import PlanPage from './Components/Plans/Plan';
import ProtectedRoute from './ProtectedRoute';
import NotFound from './Components/Homepage/NotFound';
import CheckoutForm from './Components/Plans/CheckoutForm';
function App() {

  return (<>
    <ThemeProvider theme={theme}>
      <Router>
        <Routes>
          <Route exact path="/" element={<><Navbar /><Home /></>} />
          <Route path="/user/login" element={<><Navbar /><LoginPage /></>} />
          <Route path="/user/signup" element={<SignUpPage />} />
          <Route element={<ProtectedRoute />}>
          <Route path="/user/resetpassword/:token" element={<PasswordResetPage />} />
          <Route path="/user/updateprofile/:id" element={<UpdateProfilePage />} />
          <Route path="/user/profile" element={<><Navbar /><Dashboard /></>} />
          <Route path="/user/updateprofile/:id" element={<><UpdateProfilePage /></>} />
          <Route path="/plans/createplan" element={<><Navbar /><CreatePlanPage /></>} />
          <Route path="/modifyplan/:id" element={<><Navbar /><UpdatePlanPage /></>} />
          <Route path="/reviews/createreview" element={<><Navbar /><CreateReviewPage /></>} />
          <Route path="/reviews/update/:id" element={<><Navbar /><UpdateReviewPage /></>} />
          <Route path="/admin" element={<><Navbar /><AdminDashboard /></>} />
          </Route>
          <Route path="/user/forgotpassword" element={<ForgotPasswordPage />} />
          <Route path="/plans/allplans" element={<><Navbar /><PlansPage /></>} />
          <Route path="/plans/plan/:id" element={<><Navbar /><PlanPage /></>} />
          <Route path="/checkout/:id" element={<><CheckoutForm /></>} />
          <Route path="/*" element={<NotFound />} />
        </Routes>

      </Router>
    </ThemeProvider>

  </>);
}

export default App;
