import React, { useState } from 'react';
import { Box, Paper, TextField, Button, Typography, Link, Snackbar } from '@mui/material';
import { styled } from '@mui/material/styles';
import loginimg from '../../Images/loginimg.jpg';
import { useNavigate } from "react-router-dom";
import apiInstance from './Api';
const ForgotPasswordPageContainer = styled(Box)(({ theme }) => ({
  height: '100vh',
  background: `url(${loginimg})`,
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  overflow: 'hidden', // Hide any overflowing content (white bars)
}));

const ForgotPasswordForm = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  width: '25vw',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
}));

const ForgotPasswordLink = styled(Link)(({ theme }) => ({
  marginTop: theme.spacing(1),
  cursor:"pointer"
}));

const ForgotPasswordPage = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit =async  (event) => {

    event.preventDefault();
    setLoading(true);
    const response =  await apiInstance.post(`https://gdwhln-3000.csb.app/user/forgotpassword`, {email});
    console.log(response)
    if(!response.data.resetPasswordLink)
    {
      setError("Email not Found");
    }
    else
    {
      navigate(`${response.data.resetPasswordLink}`);
    }
    setLoading(false);
   
  };

  const handleCloseAlert = () => {
    setError("");
  };

  return (
    <ForgotPasswordPageContainer id="forgot-password-container">
      <ForgotPasswordForm component="form" validate="true" onSubmit={handleSubmit}>
        <Typography variant="h5" gutterBottom>
          Forgot Password
        </Typography>
        <TextField
          label="Email"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <Button type="submit" variant="contained" color="primary" fullWidth  disabled={loading}>
          Reset Password
        </Button>
        <ForgotPasswordLink onClick={()=>{navigate("/user/login")}}>Remembered your password? Log In</ForgotPasswordLink>
      </ForgotPasswordForm>
      <Snackbar open={error} autoHideDuration={6000} onClose={handleCloseAlert} message={error} />
    </ForgotPasswordPageContainer>
  );
};

export default ForgotPasswordPage;
