import React, { useEffect,useContext, useState } from 'react';
import { useNavigate } from "react-router-dom";
import axios from 'axios';
import { AuthContext } from '../../contexts/AuthContext'
import { Typography, Box, Container, Card, CardContent, CardActions, Button, Rating, Avatar } from '@mui/material';
import { styled } from '@mui/material/styles';
import loginimg from '../../Images/loginimg.jpg';
import Reviews from './Reviews';
import apiInstance from '../Auth/Api';

const PlanPageContainer = styled(Box)(({ theme }) => ({
  marginTop: theme.spacing(4),
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  position: 'relative', // Add relative positioning to the container
  height: "100vh",
  marginTop: 0
}));

const PlanImageBackground = styled('img')(({ theme }) => ({
  width: '100%',
  height: '100%',
  objectFit: 'cover',
  position: 'absolute',
  top: 0,
  left: 0,
  zIndex: -1, // Set a lower z-index to place the image behind the content
}));

const PlanCard = styled(Card)(({ theme }) => ({
  width: '100%',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
//   borderRadius: theme.spacing(1),
  marginBottom: theme.spacing(4),
  backgroundColor: 'rgba(255, 255, 255, 0.2)', // White background with opacity (adjust opacity as needed)
  height:"50vh"
}));

const PlanCardContent = styled(CardContent)(({ theme }) => ({
  paddingBottom: theme.spacing(0),
}));

const PlanTitle = styled(Typography)(({ theme }) => ({
  fontWeight: 'bold',
  color: '#ffffff', // Change heading text color to black
  fontSize: '2rem', // Increase heading font size
}));

const PlanDescription = styled(Typography)(({ theme }) => ({
  marginTop: theme.spacing(2),
  marginBottom: theme.spacing(2),
  color: '#ffffff', // Change description text color to black
}));

const PlanRating = styled(Rating)(({ theme }) => ({
  marginBottom: theme.spacing(1),
  color: '#000000', // Change star color to black
}));

const PlanText = styled(Typography)(({ theme }) => ({
  marginBottom: theme.spacing(1),
  color: '#ffffff', // Change text color to black
}));

const PlanPage = () => {
  const navigate = useNavigate();
  const { user} = useContext(AuthContext);
  const [fetchplan,setFetchPlan]=useState(null);
  useEffect(()=>{
    async function getplans(){
      let url = window.location.href;
      let id = url.split("/");
      id = id[id.length - 1];
      const response =  await apiInstance.get(`https://gdwhln-3000.csb.app/plans/plan/${id}`);
      console.log(response)
      if(response.data.message=="plan recived")
      {
        setFetchPlan(response.data.data);
        console.log(response.data.plan);
      }
      else  if(response.data.message=="not_found")
      {
        console.log("plans not found")
      }
    }
    getplans();
  },[])
  const plan = {
    name: 'Plan 1',
    description: 'This is Plan 1 description. Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
    price: 99.99,
    rating: 4.5,
    duration: '6 months',
    discount: 20,
  };

  return (
    <PlanPageContainer>
      <PlanImageBackground src={loginimg} alt="Plan Image" />
     {fetchplan && <PlanCard>
        <PlanCardContent>
          <PlanTitle variant="h4" gutterBottom>
            {fetchplan.name}
          </PlanTitle>
          <PlanDescription variant="body1">{fetchplan.description}</PlanDescription>
          <PlanRating name="plan-rating" value={fetchplan.ratingAverage} precision={0.1} readOnly />
          <PlanText variant="body1">Plan Duration: {fetchplan.duration}</PlanText>
          <PlanText variant="body1">Plan Discount: {fetchplan.discount}%</PlanText>
          <Typography variant="h6">${fetchplan.price.toFixed(2)}</Typography>
        </PlanCardContent>
      </PlanCard>}
      <Reviews />
    </PlanPageContainer>
  );
};

export default PlanPage;
