import React, { useContext, useState } from 'react';
import { Box, Paper, TextField, Button, Typography, Snackbar, Avatar } from '@mui/material';
import { styled } from '@mui/material/styles';
import { Link } from 'react-router-dom';
import loginimg from '../../Images/loginimg.jpg';
import { useNavigate } from "react-router-dom";
import apiInstance from './Api';
import { AuthContext } from '../../contexts/AuthContext';
const UpdateProfilePageContainer = styled(Box)(({ theme }) => ({
  height: '100vh',
  background: `url(${loginimg})`,
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  overflow: 'hidden', // Hide any overflowing content (white bars)
}));

const LoginLink = styled(Link)(({ theme }) => ({
  marginTop: theme.spacing(1),
  cursor: "pointer"
}));
const UpdateProfileForm = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  width: '25vw',
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  gap: '1rem',
}));

const UpdateProfileAvatar = styled(Avatar)(({ theme }) => ({
  width: theme.spacing(12),
  height: theme.spacing(12),
  marginBottom: theme.spacing(2),
}));

const UpdateProfilePage = () => {
  const navigate = useNavigate();
  const { user,setUser} = useContext(AuthContext);
  const [name, setName] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [avatar, setAvatar] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [image, setImage] = useState(null);

  const handleNameChange = (event) => {
    setName(event.target.value);
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };

  const handleConfirmPasswordChange = (event) => {
    setConfirmPassword(event.target.value);
  };

  const handleAvatarChange = (event) => {
    const file = event.target.files[0];
    setImage(event.target.files[0]);
    console.log(event.target.files[0])
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setAvatar(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    setLoading(true);
    if (password === confirmPassword) {
      let url = window.location.href;
      let id = url.split("/");
      id = id[id.length - 1];
      const response = await apiInstance.patch(`https://gdwhln-3000.csb.app/user/updateprofile/${id}`, { name: name, password: password,profileimg:image });
      console.log(response)
      if (response.data.message == "user updated") {
        setError(response.data.message);
        setSnackbarOpen(true);
        setTimeout(()=>{
          setUser(response.data.user);
          navigate("/user/login");
        },5000)
      }
      else {
        setError(response.data.message);
        setSnackbarOpen(true);

      }
    }
    else {
      setSnackbarOpen("Passwords dont match");
    }
    setTimeout(()=>{
      setError("");
      setSnackbarOpen(false);
    },5000)
    setLoading(false);
    // setSnackbarOpen(true);
  };

  const handleCloseSnackbar = () => {
    setSnackbarOpen("");
  };

  return (
    <UpdateProfilePageContainer id="update-profile-container">
      <UpdateProfileForm component="form" validate="true" onSubmit={handleSubmit} >
        <UpdateProfileAvatar src={avatar} alt="User Avatar" />
        <Typography variant="h5" gutterBottom>
          Update Profile
        </Typography>
        <TextField
          label="Name"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={name}
          onChange={handleNameChange}
        />
        <TextField
          label="Password"
          type="password"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={password}
          onChange={handlePasswordChange}
        />
        <TextField
          label="Confirm Password"
          type="password"
          variant="outlined"
          fullWidth
          required
          margin="normal"
          value={confirmPassword}
          onChange={handleConfirmPasswordChange}
        />
        <input type="file" accept="image/*" name="image" id="avatar-input" style={{ display: 'none' }} onChange={handleAvatarChange} />
        <label htmlFor="avatar-input">
          <Button variant="contained" color="primary" component="span">
            Upload Image
          </Button>
        </label>
        <Button variant="contained" color="primary" fullWidth type="submit" >
          Update Profile
        </Button>
        <LoginLink to="/signup">Don't have an account? Sign Up</LoginLink>
      </UpdateProfileForm>
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={6000}
        onClose={handleCloseSnackbar}
        message={error}
      />
    </UpdateProfilePageContainer>
  );
};

export default UpdateProfilePage;
