import React, { useEffect, useState } from 'react';
import { Grid, Typography, Card, Button, Avatar, Rating } from '@mui/material';
import { styled } from '@mui/material/styles';
import { Link } from 'react-router-dom';
import apiInstance from '../Auth/Api';
const TestimonialsContainer = styled(Grid)(({ theme }) => ({
  backgroundColor: '#fff', // Set background color to white
  textAlign: 'center',
  padding: theme.spacing(4),
  width: '100vw',
  display: 'flex',
  justifyContent: 'center', // Center the cards horizontally
  alignItems: 'stretch', // Stretch the cards to have equal height
  flexWrap: 'wrap', // Allow testimonials to wrap into multiple rows
}));

const TestimonialCard = styled(Card)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  justifyContent: 'center',
  padding: theme.spacing(2),
  margin: theme.spacing(2),
  width: '25vw', // Make each card occupy one-third of the viewport width with some spacing
  borderRadius: theme.spacing(2),
  boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)', // Add box shadow to the card
  background: '#eaeaea',
  color: '#000',
  flex: '1 1 33.3333%', // Use flex property to set equal width for all cards
  transition: 'box-shadow 0.3s ease', // Add transition effect for box shadow
  '&:hover': {
    boxShadow: '0 8px 16px rgba(0, 0, 0, 0.2)', // Add hover effect for box shadow
    cursor: 'pointer', // Change cursor on hover
  },
}));

const ReviewerAvatar = styled(Avatar)(({ theme }) => ({
  width: theme.spacing(8),
  height: theme.spacing(8),
  marginBottom: theme.spacing(2),
}));

const ReviewDate = styled(Typography)(({ theme }) => ({
  
  color: theme.palette.text.secondary,
}));

const Testimonials = () => {
  const [testimonials, setTestimonials] = useState([]);
  
  useEffect(()=>{
    async function getplans(){
      const response =  await apiInstance.get(`https://gdwhln-3000.csb.app/reviews/top3`);
      console.log(response)
      if(response.data.message=="top_3_reviews")
      {
        setTestimonials(response.data.reviews);
        console.log(response.data.reviews);
      }
      else  if(response.data.message=="reviews_not_found")
      {
        console.log("reviews not found")
      }
    }
    getplans();
  },[])
  

  return (
    <TestimonialsContainer container>
      <Grid item xs={12}>
        <Typography variant="h3" gutterBottom>
          Reviews
        </Typography>
      </Grid>
      {testimonials && testimonials.map((testimonial) => (
        <Grid item key={testimonial._id}>
          <TestimonialCard>
            <ReviewerAvatar alt={`${testimonial.user.name}`} src={testimonial.user.name} />
            <Typography variant="h6" gutterBottom>
              {testimonial.user.name}
            </Typography>
            <Typography variant="subtitle1" gutterBottom>
              {testimonial.plan.name}
            </Typography>
            <Typography variant="body1" gutterBottom>
              {testimonial.review}
            </Typography>
            <Rating
              name="rating"
              value={testimonial.rating}
              precision={0.5}
              readOnly
              emptyIcon={<span ></span>}
              icon={<span style={{ color: '#8bc34a' }}></span>}
            />
            <ReviewDate variant="h6">{new Date(testimonial.createdAt).toDateString()}</ReviewDate>
            <Link to={`/plans/plan/${testimonial.plan._id}`} style={{ textDecoration: 'none' }}>
            <Button variant="contained" color="primary">
              Plan
            </Button>
            </Link>
          </TestimonialCard>
        </Grid>
      ))}
    </TestimonialsContainer>
  );
};

export default Testimonials;
