import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { TextField, Button, Snackbar, Typography, Box, Rating, Grid, Avatar } from '@mui/material';
import { styled } from '@mui/material/styles';
import loginimg from '../../Images/loginimg.jpg';

import apiInstance from '../Auth/Api';


const UpdateReviewPageContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  justifyContent: "center",
  alignItems: 'center',
  backgroundImage: `url(${loginimg})`, // Apply the background image
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  height: '110vh',
}));

const FormContainer = styled(Box)(({ theme }) => ({
  marginTop: theme.spacing(2),
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  background: 'white', // White background for form container
  padding: theme.spacing(2),
  borderRadius: theme.spacing(1), // Rounded corners
  width: "30vw",
}));

const RatingContainer = styled(Grid)({
  marginTop: 8, // Reduced the gap
  marginBottom: 8, // Reduced the gap
});

const UpdateReviewButton = styled(Button)({
  marginTop: 16,
});

const UpdateReviewPage = () => {
  const navigate = useNavigate();
  const [fetchedReview,SetFetchedReview]=useState(null);
  const [reviewText, setReviewText] = useState(''); // Set the existing review text here
  const [rating, setRating] = useState(0); // Set the existing rating here
  const [errorSnackbarOpen, setErrorSnackbarOpen] = useState(false);
  const [error,setError]=useState("");
  useEffect(() => {
    async function getReviewdata() {
      let url = window.location.href;
      let id = url.split("/");
      id = id[id.length - 1];
      const response = await apiInstance.get(`https://gdwhln-3000.csb.app/reviews/review/${id}`);
      console.log(response)
      if (response.data.message == "reviews found") {
        SetFetchedReview(response.data.review);
        console.log(fetchedReview);
      }
      else if (response.data.message == "review not found") {
        console.log("review not found")
      }
    
    }
    getReviewdata();

  }, [])
  const handleUpdateReview = async () => {
    const response = await apiInstance.patch(`https://gdwhln-3000.csb.app/reviews/review/${fetchedReview._id}`,{review:reviewText,rating:rating});
      console.log(response)
      if (response.data.message == "Review updated") {
        setErrorSnackbarOpen(true);
        setError("Review Updated")
        
      }
      else if (response.data.message == "review not found") {
        setErrorSnackbarOpen(true);
        setError("Review Updation Failed")
      }
    setTimeout(()=>{
      setError("");
      setErrorSnackbarOpen(false);
    },5000)
  };

  const handleSnackbarClose = () => {
    setErrorSnackbarOpen(false);
  };

  return (
    <UpdateReviewPageContainer>
      {fetchedReview && <FormContainer>
        <Typography variant="h4" gutterBottom>
          Update Review
        </Typography>
        <Typography variant="body1" gutterBottom>
          Plan: {fetchedReview.plan.name}
        </Typography>
        <TextField
          label="Review"
          variant="outlined"
          fullWidth
          multiline
          rows={3} // Reduce the height of the review field
          margin="normal"
          value={reviewText}
          onChange={(e) => setReviewText(e.target.value)}
        />
        {/* Add file input for plan image */}
        <RatingContainer container spacing={2}>
          <Grid item xs={6}>
            <Typography variant="body1">Rating</Typography>
          </Grid>
          <Grid item xs={6}>
            <Rating
              name="rating"
              value={rating}
              onChange={(event, newValue) => setRating(newValue)}
              size="large"
              max={5}
              precision={1}
              emptyIcon={<span style={{ color: '#e0e0e0' }}></span>}
              icon={<span style={{ color: '#4caf50' }}></span>}
            />
          </Grid>
        </RatingContainer>
        <UpdateReviewButton variant="outlined" onClick={handleUpdateReview}>
          Update Review
        </UpdateReviewButton>
      </FormContainer>}
      <Snackbar open={errorSnackbarOpen} autoHideDuration={6000} onClose={handleSnackbarClose} message={error} />
    </UpdateReviewPageContainer>
  );
};

export default UpdateReviewPage;
