import React, { useState ,useContext, useEffect} from 'react';
import { useNavigate } from 'react-router-dom';
import { TextField, Button, Snackbar, Typography, Box, Rating, Grid, Avatar, MenuItem, Select, FormControl, InputLabel } from '@mui/material';
import { styled } from '@mui/material/styles';
import loginimg from '../../Images/loginimg.jpg';
import { AuthContext } from '../../contexts/AuthContext'
import apiInstance from '../Auth/Api';
const CreateReviewPageContainer = styled(Box)(({ theme }) => ({
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  justifyContent:"center",
  backgroundImage: `url(${loginimg})`, // Apply the background image
  backgroundSize: 'cover',
  backgroundPosition: 'center',
  height: '110vh',
}));

const FormContainer = styled(Box)(({ theme }) => ({
  marginTop: theme.spacing(2),
  display: 'flex',
  flexDirection: 'column',
  alignItems: 'center',
  background: 'white', // White background for form container
  padding: theme.spacing(2),
  borderRadius: theme.spacing(1), // Rounded corners
  width: "30vw",
}));

const RatingContainer = styled(Grid)({
  marginTop: 8, // Reduced the gap
  marginBottom: 8, // Reduced the gap
});

const CreateReviewButton = styled(Button)({
  marginTop: 16,
});

const CreateReviewPage = () => {
  const navigate = useNavigate();
  const { user, login, logout ,autherror,setauthError} = useContext(AuthContext);
  const [reviewText, setReviewText] = useState('');
  const [plans, setPlans] = useState([]);
  const [rating, setRating] = useState(0);
  const [selectedPlan, setSelectedPlan] = useState('');
  const [errorSnackbarOpen, setErrorSnackbarOpen] = useState(false);
  const [loading,setLoading]=useState(false);
  const [error,setError]=useState('');

  const  handleCreateReview = async (event) => {
    event.preventDefault();
    setLoading(true)
    if(selectedPlan=='')
    {
      setError("Please Select a Plan")
      setErrorSnackbarOpen(true);
      setTimeout(()=>{
        setError("")
        setErrorSnackbarOpen(false);
      },5000)
      return;
    }
    const response = await apiInstance.post(`https://gdwhln-3000.csb.app/reviews/${selectedPlan}`,{plan:selectedPlan,review:reviewText,rating:rating,user:user._id});
    console.log(response)
    if(response.data.review!=null)
    {
      setError("Review Created")
      setErrorSnackbarOpen(true);
      // setPlans("")
      setRating(0)
      setReviewText("");
      setSelectedPlan("")
      setTimeout(()=>{
        setError("")
        setErrorSnackbarOpen(false);
       
      },5000)
    }
    else
    {
      setError(response.data.message)
      setErrorSnackbarOpen(true);
      setTimeout(()=>{
        setError("")
        setErrorSnackbarOpen(false);
      },5000)
    }
    setLoading(false)
 
  };
  useEffect(()=>{
    if(user==null)
    {
      navigate("/user/login");
    }
      async function fetchallplans(){
        const response = await apiInstance.get(`https://gdwhln-3000.csb.app/plans/allplans`);
        console.log(response)
        if (response.data.plans) {
            setPlans(response.data.plans);
        }
        else
        {
          setErrorSnackbarOpen(response.data.message)    
        }
      }
      fetchallplans();
  },[])
  const handleSnackbarClose = () => {
    setErrorSnackbarOpen(false);
  };

  return (
    <CreateReviewPageContainer>
      <FormContainer  component="form" validate="true" onSubmit={handleCreateReview}>
        <Typography variant="h4" gutterBottom>
          Create Review
        </Typography>
        <FormControl fullWidth variant="outlined" margin="normal">
          <InputLabel>Plan</InputLabel>
          <Select
            value={selectedPlan}
            onChange={(e) => {setSelectedPlan(e.target.value)}}

            label="Plan"
          >
            {plans && plans.map((plan)=>(<MenuItem value={plan._id}>{plan.name}</MenuItem>))}
           
            {/* Add more menu items based on the plans you have */}
          </Select>
        </FormControl>
        <TextField
          label="Review"
          variant="outlined"
          fullWidth
          required
          multiline
          rows={3} // Reduce the height of the review field
          margin="normal"
          value={reviewText}
          onChange={(e) => setReviewText(e.target.value)}
        />
        {/* Add file input for plan image */}
        <RatingContainer container spacing={2}>
          <Grid item xs={6}>
            <Typography variant="body1">Rating</Typography>
          </Grid>
          <Grid item xs={6}>
            <Rating
              name="rating"
              value={rating}
             
              onChange={(event, newValue) => setRating(newValue)}
              size="large"
              max={5}
              precision={1}
              emptyIcon={<span style={{ color: '#e0e0e0' }}></span>}
              icon={<span style={{ color: '#4caf50' }}></span>}
            />
          </Grid>
        </RatingContainer>
        <CreateReviewButton type="submit" variant="outlined" disabled={loading} >
          Create Review
        </CreateReviewButton>
      </FormContainer>
      <Snackbar open={errorSnackbarOpen} autoHideDuration={6000} onClose={handleSnackbarClose} message={error} />
    </CreateReviewPageContainer>
  );
};

export default CreateReviewPage;
